.recipe {
    max-width: 1200px;
    margin: 40px auto;
    text-align: center;
    background-color: #ffffff;
    padding: 40px;
    box-sizing: border-box;
}

.recipe ul {
    display: flex;
    padding: 0;
    justify-content: center;
    margin-top: 0;
}

.recipe li {
    list-style-type: none;
    margin-right: 10px;
    color: #777777;
}

.recipe li::after {
    content: ",";
}

.recipe li:last-child::after {
    content: ".";
}

.method {
    text-align: left;
    line-height: 1.5em;
}

.recipe.dark {
    background-color: #555555;
    color: #e4e4e4;
}
